
import Vue from 'vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import Group from 'vux/src/components/group/index.vue'
import LoadingPlugin from 'vux/src/plugins/loading/index.js'

import PullTo from 'vue-pull-to'
import { checkLoginToLogin } from '@/api/login'
import * as base from '@/api/base'
import { agentQuery } from '@/api/keen'
Vue.use(LoadingPlugin)
export default {
components: {
PullTo,
XHeader,
Flexbox,
FlexboxItem,
XButton,
Checker,
CheckerItem,
Datetime,
Group
},
data () {
return {
dataList: [],
demo: 1,
value1: '', // 选择日期
iconLink: '',
result: 0 
}
},
methods: {
loadmore (loaded) {
setTimeout(() => {
this.dataList = this.dataList.concat(this.dataList)
loaded('done')
}, 2000)
},
stateChange (state) {
console.log(state)
if (state === 'pull' || state === 'trigger') {
this.iconLink = '#icon-arrow-bottom'
} else if (state === 'loading') {
this.iconLink = '#icon-loading'
} else if (state === 'loaded-done') {
this.iconLink = '#icon-finish'
}
},
change (value) {
agentQuery.queryAgentOneDayIncome(base.getSingleUserInfo('user_agentId'), value, data => {
this.result = data.result
this.$vux.toast.text('查询成功', 'middle')
})
}
},
created: function () {
checkLoginToLogin(this)
let today = new Date()
this.value1 = today.getFullYear() + '-' + (today.getMonth() + 1) + '-' + today.getDate()
agentQuery.queryAgentOneDayIncome(base.getSingleUserInfo('user_agentId'), this.value1, data => {
this.result = data.result
})
}
}



